.visually-hidden,
input[type="file"].visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.scroll-lock {
  overflow-y: auto;
  position: fixed;
}

.btn-reset {
  cursor: pointer;
  padding: 0;
  background: $color-transparent;
  border: none;
}

.section-title {
  margin: 0 0 5px;
  font-family: "RobotoCondensed";
  font-weight: bold;
  font-size: 54px;
  letter-spacing: -0.02em;
  line-height: 63px;
  text-align: center;
  color: $color-black;

  @media screen and (max-width: 767px) {
    font-size: 34px;
    line-height: 33px;
  }
}

.section-description {
  margin: 0;
  font-weight: normal;
  font-size: 18px;
  letter-spacing: -0.03em;
  line-height: 26px;
  text-align: center;
  color: #9dafbd;
}

.bg-wrapper {
  position: relative;
  z-index: 9999;
  background-color: #ffffff;
}
